lmHY;IACE,aAAA;IACA,oCAAA;EzBomHd;EyBtmHY;IACE,aAAA;IACA,oCAAA;EzBwmHd;EyB1mHY;IACE,aAAA;IACA,oCAAA;EzB4mHd;EyB9mHY;IACE,aAAA;IACA,oCAAA;EzBgnHd;EyBlnHY;IACE,aAAA;IACA,oCAAA;EzBonHd;EyBtnHY;IACE,aAAA;IACA,oCAAA;EzBwnHd;AACF;ASr0HE;EgBmGA;IAG4C,YAAA;EzBmuH5C;EyBpoHY;IACE,aAAA;IACA,0BAAA;EzBsoHd;EyBnoHY;IACE,YAAA;IACA,oCAAA;EzBqoHd;EyBvoHY;IACE,YAAA;IACA,oCAAA;EzByoHd;EyB3oHY;IACE,YAAA;IACA,oCAAA;EzB6oHd;EyB/oHY;IACE,YAAA;IACA,oCAAA;EzBipHd;EyBnpHY;IACE,YAAA;IACA,oCAAA;EzBqpHd;EyBvpHY;IACE,YAAA;IACA,oCAAA;EzBypHd;EyB3pHY;IACE,YAAA;IACA,oCAAA;EzB6pHd;EyB/pHY;IACE,YAAA;IACA,oCAAA;EzBiqHd;EyBnqHY;IACE,YAAA;IACA,oCAAA;EzBqqHd;AACF;;AyBxpHA;EAAa,eAAA;AzB4pHb;;AyBzpHA;EAAa,aAAA;AzB6pHb;;AyB1pHA;EACE,aAAA;EACA,mBAAA;AzB6pHF;;AyBzpHA;EACE,iCAAA;AzB4pHF;;AyBzpHA;EAAY,aAAA;AzB6pHZ;;AyB5pHA;EACE,aAAA;EACA,oBAAA;AzB+pHF;;AyB7pHA;EACE,aAAA;EACA,uBAAA;AzBgqHF;;AyB7pHA;EAAkB,qBAAA;AzBiqHlB;;AyB/pHA;EAAgB,kBAAA;AzBmqHhB;;AyBjqHA;EACE,gBAAA;EACA,uBAAA;EACA,mBAAA;AzBoqHF;;AyBjqHA;EAAe,iBAAA;AzBqqHf;;AyBnqHA;EAAqB,oCAAA;AzBuqHrB;;AyBrqHA;EAAe,oCAAA;AzByqHf;;AyBvqHA;EACE,kBAAA;EACA,iBAAA;AzB0qHF;;AA97HA;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;CAAA;AA6DA;;oEAAA;AAmBA;;;oEAAA;AAYA;;oEAAA;AAIA,yDAAA;AASA,yDAAA;AAaA,yDAAA","sourcesContent":["/*  \n▼ Attention\n - Predictable (予測性)\n\n - Resusable (再利用性)\n\n - Sercurable (保守性)\n\n - Extendable (拡張性)\n\n - ID is only for JavaScript fooks (CLASS is the alternative)\n\n - Priority : !important > class > element > *\n\n - NO emittion (e.g. ○ button -> btn)\n - OK emittion (e.g. information -> info) * long and easy-to-know words\n\n - Based on meaning but not look/location (× text-red  ○ text-attention)\n\n - Apply only necessary styles to element selector (body, a, img)\n\n - Don't depend on html (× .content > div > div)\n\n - Don't complicate stylings (× li.item  ○ .item)\n\n\n▼ CSS structure\n - SMACSS (Scalable and Modular Architecture)\n   - Base : default/reset css (body, a, img)\n   - Layout : header, footer (l-)\n   - Module: reusable components (.button, .title)\n   - State : fooks for js (is-open) \n   - Theme : theme swicher (.theme-dark)\n\n - BEMCSS (Block Element Modifier)\n   - Block__Element–-Modifier (up to preference)\n      -> block-element--modifier\n      -> _ for long naming (* block_name)\n   - (e.g.) header-nav__item--contact\n   - contact(modifer) is set to make itself outstanding\n\nSassを使用している場合は以下のルール\n   ローカル変数を最初に定義します\n   @extendをローカル変数の次に指定します\n   @mixinを@extendの次に指定します\n   @contentを使用している@mixinは最後に指定します\n\n\n▼ プロパティ宣言順\nmixin\nボックスモデルの種類や表示方法を示すプロパティ（box-sizing, display, visibility, floatなど）\n位置情報に関するプロパティ（position, z-indexなど）\nボックスモデルのサイズに関するプロパティ（width, height, margin, padding, borderなど）\nフォント関連のプロパティ（font-size, line-heightなど）\n色に関するプロパティ（color, background-colorなど）\nそれ以外\n@contentを使用している@mixin\n*/\n\n\n\n/*-------------------------------------------------------------------\n|  Foundation                                                       |\n-------------------------------------------------------------------*/\n\n@use 'foundation/vendors' with (\n   // bootstrap内の変数の上書き\n   $grid-breakpoints: (\n      sp:    320px,\n      tab:   768px,\n      pc:    1024px\n   ),\n   $grid-columns: 24\n);\n@use 'foundation/reset';\n@use 'foundation/base';\n\n\n\n\n/*-------------------------------------------------------------------\n|  Layout                                                           |\n|  ワイヤーフレームに定義されるような大きなコンテナブロック(IDセレクタ指定可)   |\n-------------------------------------------------------------------*/\n\n@use 'layout/header';\n@use 'layout/main';\n@use 'layout/footer';\n\n\n\n\n/*-------------------------------------------------------------------\n|  Object                                                           |\n-------------------------------------------------------------------*/\n\n/*  Component  -----------------------------------------*/\n// Component 再利用できるような小さなモジュール、固有の幅や装飾的なスタイルは極力指定しない\n@use 'object/component/button';\n@use 'object/component/card';\n@use 'object/component/wrapper';\n@use 'object/component/menu';\n@use 'object/component/partition';\n\n\n/*   Project   -----------------------------------------*/\n// プロジェクト固有のUI(殆どのスタイルの追加はこのレイヤー)\n@use 'object/project/form';\n@use 'object/project/modal';\n@use 'object/project/slide';\n@use 'object/project/management';\n@use 'object/project/shared';\n\n@use 'object/project/admin/user';\n@use 'object/project/user/entry';\n@use 'object/project/user/index';\n\n\n/*   Utility   -----------------------------------------*/\n@use 'object/utility/utility';","// _vendors.scss\n\n/*-------------------------------------------------------------------\n|  Bootstrap                                                        |\n-------------------------------------------------------------------*/\n\n// 関数（カラー、SVG、計算などが操作できるように）\n@import \"~bootstrap/scss/functions\";\n\n// 変数\n@import \"~bootstrap/scss/variables\";\n\n// ツール (map, mixin, grid機能など)\n@import \"~bootstrap/scss/maps\";\n@import \"~bootstrap/scss/mixins\";\n@import \"~bootstrap/scss/grid\";\n\n// Bootstrapから輸入したsassを上書き\n.row, .row > * {\n  padding: 0;\n  margin: 0;\n}","// Row\n//\n// Rows contain your columns.\n\n@if $enable-grid-classes {\n  .row {\n    @include make-row();\n\n    > * {\n      @include make-col-ready();\n    }\n  }\n}\n\n@if $enable-cssgrid {\n  .grid {\n    display: grid;\n    grid-template-rows: repeat(var(--#{$prefix}rows, 1), 1fr);\n    grid-template-columns: repeat(var(--#{$prefix}columns, #{$grid-columns}), 1fr);\n    gap: var(--#{$prefix}gap, #{$grid-gutter-width});\n\n    @include make-cssgrid();\n  }\n}\n\n\n// Columns\n//\n// Common styles for small and large grid columns\n\n@if $enable-grid-classes {\n  @include make-grid-columns();\n}\n","// Grid system\n//\n// Generate semantic grid columns with these mixins.\n\n@mixin make-row($gutter: $grid-gutter-width) {\n  --#{$prefix}gutter-x: #{$gutter};\n  --#{$prefix}gutter-y: 0;\n  display: flex;\n  flex-wrap: wrap;\n  // TODO: Revisit calc order after https://github.com/react-bootstrap/react-bootstrap/issues/6039 is fixed\n  margin-top: calc(-1 * var(--#{$prefix}gutter-y)); // stylelint-disable-line function-disallowed-list\n  margin-right: calc(-.5 * var(--#{$prefix}gutter-x)); // stylelint-disable-line function-disallowed-list\n  margin-left: calc(-.5 * var(--#{$prefix}gutter-x)); // stylelint-disable-line function-disallowed-list\n}\n\n@mixin make-col-ready() {\n  // Add box sizing if only the grid is loaded\n  box-sizing: if(variable-exists(include-column-box-sizing) and $include-column-box-sizing, border-box, null);\n  // Prevent columns from becoming too narrow when at smaller grid tiers by\n  // always setting `width: 100%;`. This works because we set the width\n  // later on to override this initial width.\n  flex-shrink: 0;\n  width: 100%;\n  max-width: 100%; // Prevent `.col-auto`, `.col` (& responsive variants) from breaking out the grid\n  padding-right: calc(var(--#{$prefix}gutter-x) * .5); // stylelint-disable-line function-disallowed-list\n  padding-left: calc(var(--#{$prefix}gutter-x) * .5); // stylelint-disable-line function-disallowed-list\n  margin-top: var(--#{$prefix}gutter-y);\n}\n\n@mixin make-col($size: false, $columns: $grid-columns) {\n  @if $size {\n    flex: 0 0 auto;\n    width: percentage(divide($size, $columns));\n\n  } @else {\n    flex: 1 1 0;\n    max-width: 100%;\n  }\n}\n\n@mixin make-col-auto() {\n  flex: 0 0 auto;\n  width: auto;\n}\n\n@mixin make-col-offset($size, $columns: $grid-columns) {\n  $num: divide($size, $columns);\n  margin-left: if($num == 0, 0, percentage($num));\n}\n\n// Row columns\n//\n// Specify on a parent element(e.g., .row) to force immediate children into NN\n// number of columns. Supports wrapping to new lines, but does not do a Masonry\n// style grid.\n@mixin row-cols($count) {\n  > * {\n    flex: 0 0 auto;\n    width: divide(100%, $count);\n  }\n}\n\n// Framework grid generation\n//\n// Used only by Bootstrap to generate the correct number of grid classes given\n// any value of `$grid-columns`.\n\n@mixin make-grid-columns($columns: $grid-columns, $gutter: $grid-gutter-width, $breakpoints: $grid-breakpoints) {\n  @each $breakpoint in map-keys($breakpoints) {\n    $infix: breakpoint-infix($breakpoint, $breakpoints);\n\n    @include media-breakpoint-up($breakpoint, $breakpoints) {\n      // Provide basic `.col-{bp}` classes for equal-width flexbox columns\n      .col#{$infix} {\n        flex: 1 0 0%; // Flexbugs #4: https://github.com/philipwalton/flexbugs#flexbug-4\n      }\n\n      .row-cols#{$infix}-auto > * {\n        @include make-col-auto();\n      }\n\n      @if $grid-row-columns > 0 {\n        @for $i from 1 through $grid-row-columns {\n          .row-cols#{$infix}-#{$i} {\n            @include row-cols($i);\n          }\n        }\n      }\n\n      .col#{$infix}-auto {\n        @include make-col-auto();\n      }\n\n      @if $columns > 0 {\n        @for $i from 1 through $columns {\n          .col#{$infix}-#{$i} {\n            @include make-col($i, $columns);\n          }\n        }\n\n        // `$columns - 1` because offsetting by the width of an entire row isn't possible\n        @for $i from 0 through ($columns - 1) {\n          @if not ($infix == \"\" and $i == 0) { // Avoid emitting useless .offset-0\n            .offset#{$infix}-#{$i} {\n              @include make-col-offset($i, $columns);\n            }\n          }\n        }\n      }\n\n      // Gutters\n      //\n      // Make use of `.g-*`, `.gx-*` or `.gy-*` utilities to change spacing between the columns.\n      @each $key, $value in $gutters {\n        .g#{$infix}-#{$key},\n        .gx#{$infix}-#{$key} {\n          --#{$prefix}gutter-x: #{$value};\n        }\n\n        .g#{$infix}-#{$key},\n        .gy#{$infix}-#{$key} {\n          --#{$prefix}gutter-y: #{$value};\n        }\n      }\n    }\n  }\n}\n\n@mixin make-cssgrid($columns: $grid-columns, $breakpoints: $grid-breakpoints) {\n  @each $breakpoint in map-keys($breakpoints) {\n    $infix: breakpoint-infix($breakpoint, $breakpoints);\n\n    @include media-breakpoint-up($breakpoint, $breakpoints) {\n      @if $columns > 0 {\n        @for $i from 1 through $columns {\n          .g-col#{$infix}-#{$i} {\n            grid-column: auto / span $i;\n          }\n        }\n\n        // Start with `1` because `0` is and invalid value.\n        // Ends with `$columns - 1` because offsetting by the width of an entire row isn't possible.\n        @for $i from 1 through ($columns - 1) {\n          .g-start#{$infix}-#{$i} {\n            grid-column-start: $i;\n          }\n        }\n      }\n    }\n  }\n}\n","// Breakpoint viewport sizes and media queries.\n//\n// Breakpoints are defined as a map of (name: minimum width), order from small to large:\n//\n//    (xs: 0, sm: 576px, md: 768px, lg: 992px, xl: 1200px, xxl: 1400px)\n//\n// The map defined in the `$grid-breakpoints` global variable is used as the `$breakpoints` argument by default.\n\n// Name of the next breakpoint, or null for the last breakpoint.\n//\n//    >> breakpoint-next(sm)\n//    md\n//    >> breakpoint-next(sm, (xs: 0, sm: 576px, md: 768px, lg: 992px, xl: 1200px, xxl: 1400px))\n//    md\n//    >> breakpoint-next(sm, $breakpoint-names: (xs sm md lg xl xxl))\n//    md\n@function breakpoint-next($name, $breakpoints: $grid-breakpoints, $breakpoint-names: map-keys($breakpoints)) {\n  $n: index($breakpoint-names, $name);\n  @if not $n {\n    @error \"breakpoint `#{$name}` not found in `#{$breakpoints}`\";\n  }\n  @return if($n < length($breakpoint-names), nth($breakpoint-names, $n + 1), null);\n}\n\n// Minimum breakpoint width. Null for the smallest (first) breakpoint.\n//\n//    >> breakpoint-min(sm, (xs: 0, sm: 576px, md: 768px, lg: 992px, xl: 1200px, xxl: 1400px))\n//    576px\n@function breakpoint-min($name, $breakpoints: $grid-breakpoints) {\n  $min: map-get($breakpoints, $name);\n  @return if($min != 0, $min, null);\n}\n\n// Maximum breakpoint width.\n// The maximum value is reduced by 0.02px to work around the limitations of\n// `min-` and `max-` prefixes and viewports with fractional widths.\n// See https://www.w3.org/TR/mediaqueries-4/#mq-min-max\n// Uses 0.02px rather than 0.01px to work around a current rounding bug in Safari.\n// See https://bugs.webkit.org/show_bug.cgi?id=178261\n//\n//    >> breakpoint-max(md, (xs: 0, sm: 576px, md: 768px, lg: 992px, xl: 1200px, xxl: 1400px))\n//    767.98px\n@function breakpoint-max($name, $breakpoints: $grid-breakpoints) {\n  $max: map-get($breakpoints, $name);\n  @return if($max and $max > 0, $max - .02, null);\n}\n\n// Returns a blank string if smallest breakpoint, otherwise returns the name with a dash in front.\n// Useful for making responsive utilities.\n//\n//    >> breakpoint-infix(xs, (xs: 0, sm: 576px, md: 768px, lg: 992px, xl: 1200px, xxl: 1400px))\n//    \"\"  (Returns a blank string)\n//    >> breakpoint-infix(sm, (xs: 0, sm: 576px, md: 768px, lg: 992px, xl: 1200px, xxl: 1400px))\n//    \"-sm\"\n@function breakpoint-infix($name, $breakpoints: $grid-breakpoints) {\n  @return if(breakpoint-min($name, $breakpoints) == null, \"\", \"-#{$name}\");\n}\n\n// Media of at least the minimum breakpoint width. No query for the smallest breakpoint.\n// Makes the @content apply to the given breakpoint and wider.\n@mixin media-breakpoint-up($name, $breakpoints: $grid-breakpoints) {\n  $min: breakpoint-min($name, $breakpoints);\n  @if $min {\n    @media (min-width: $min) {\n      @content;\n    }\n  } @else {\n    @content;\n  }\n}\n\n// Media of at most the maximum breakpoint width. No query for the largest breakpoint.\n// Makes the @content apply to the given breakpoint and narrower.\n@mixin media-breakpoint-down($name, $breakpoints: $grid-breakpoints) {\n  $max: breakpoint-max($name, $breakpoints);\n  @if $max {\n    @media (max-width: $max) {\n      @content;\n    }\n  } @else {\n    @content;\n  }\n}\n\n// Media that spans multiple breakpoint widths.\n// Makes the @content apply between the min and max breakpoints\n@mixin media-breakpoint-between($lower, $upper, $breakpoints: $grid-breakpoints) {\n  $min: breakpoint-min($lower, $breakpoints);\n  $max: breakpoint-max($upper, $breakpoints);\n\n  @if $min != null and $max != null {\n    @media (min-width: $min) and (max-width: $max) {\n      @content;\n    }\n  } @else if $max == null {\n    @include media-breakpoint-up($lower, $breakpoints) {\n      @content;\n    }\n  } @else if $min == null {\n    @include media-breakpoint-down($upper, $breakpoints) {\n      @content;\n    }\n  }\n}\n\n// Media between the breakpoint's minimum and maximum widths.\n// No minimum for the smallest breakpoint, and no maximum for the largest one.\n// Makes the @content apply only to the given breakpoint, not viewports any wider or narrower.\n@mixin media-breakpoint-only($name, $breakpoints: $grid-breakpoints) {\n  $min:  breakpoint-min($name, $breakpoints);\n  $next: breakpoint-next($name, $breakpoints);\n  $max:  breakpoint-max($next, $breakpoints);\n\n  @if $min != null and $max != null {\n    @media (min-width: $min) and (max-width: $max) {\n      @content;\n    }\n  } @else if $max == null {\n    @include media-breakpoint-up($name, $breakpoints) {\n      @content;\n    }\n  } @else if $min == null {\n    @include media-breakpoint-down($next, $breakpoints) {\n      @content;\n    }\n  }\n}\n","/* http://meyerweb.com/eric/tools/css/reset/ \n  v2.0 | 20110126\n  License: none (public domain)\n*/\n\nhtml, body, div, span, applet, object, iframe,\nh1, h2, h3, h4, h5, h6, p, blockquote, pre,\na, abbr, acronym, address, big, cite, code,\ndel, dfn, em, img, ins, kbd, q, s, samp,\nsmall, strike, strong, sub, sup, tt, var,\nb, u, i, center,\ndl, dt, dd, ol, ul, li,\nfieldset, form, label, legend,\ntable, caption, tbody, tfoot, thead, tr, th, td,\narticle, aside, canvas, details, embed, \nfigure, figcaption, footer, header, hgroup, \nmenu, nav, output, ruby, section, summary,\ntime, mark, audio, video {\n  margin: 0;\n  padding: 0;\n  border: 0;\n  font: inherit;\n  vertical-align: baseline;\n}\n/* HTML5 display-role reset for older browsers */\narticle, aside, details, figcaption, figure, \nfooter, header, hgroup, menu, nav, section {\n\tdisplay: block;\n}\n// body {\n// \tline-height: 1;\n// }\nol, ul {\n  list-style: none;\n}\nblockquote, q {\n  quotes: none;\n}\nblockquote:before, blockquote:after,\nq:before, q:after {\n  content: '';\n  content: none;\n}\ntable {\n  border-collapse: collapse;\n  border-spacing: 0;\n}","/** #Index ... 各項目に # を付けて検索できます\n  ▼ Bootstrapにて使用される変数の上書き\n * MediaQuery..........メディアクエリ時に使用する変数\n\n  ▼ 自作の変数\n * Color...............色の変数 (font, bg, btn, border)\n * Font................fontの変数(size, family)\n * Line height.........line-heightのサイズを定める変数\n * Border..............borderのサイズを定める変数\n * Spacing.............要素の外/内側に設ける空間を定める変数(margin, padding)\n * Layout..............Layout配下の要素で、操作を受けやすいプロパティ値を変数化\n * Project.............Project配下の要素で、操作を受けやすいプロパティ値を変数化\n * \n * \n * \n * \n * \n * \n * \n**/\n\n\n/*-------------------------------------\n|   #Bootstrapにて使用される変数の上書き   |\n-------------------------------------*/\n\n/*  #MediaQuery  ---------------------------------------------*/\n\n// bootstrapのgrid機能にて使用\n$grid-breakpoints: (\n  sp:    480px,\n  tab:   768px,\n  pc:    1024px\n);\n\n/*  使用方法   //! 要記載\nhtmlでのクラス記載...\n\nrow\nrow-cols-sp-1\nrow-cols-tab-2\nrow-cols-pc-4\n\ncol\n*/\n\n\n\n\n/*---------------\n|   #自作の変数   |\n---------------*/\n\n/*  #Color  ---------------------------------------------*/\n\n// 各色の変数\n$blue:       #0d6efd !default;\n$pink:       #d63384 !default;\n$red:        #ff0000 !default;\n$orange:     #fd7e14 !default;\n$yellow:     #ffc107 !default;\n$green:      #198754 !default;\n$teal:       #20c997 !default;\n$skyblue:    #159ed0 !default;\n$white:      #fff !default;\n$black:      #1b1b1b !default;\n$black-tp:   #1b1b1bc7 !default;\n\n$gray-2xl:   #888 !default;\n$gray-xl:    #777 !default;\n$gray-l:     #666 !default;\n$gray-md:    #555 !default;\n$gray-h:     #444 !default;\n$gray-xh:    #333 !default;\n$gray-2xh:   #222 !default;\n\n// フォントの色\n$font-colors: (\n  \"primary\":       $white,\n  \"secondary\":     $gray-xl,\n  \"white-back\":    $black,\n  \"date\":          $gray-2xl,\n  \"dcmnt\":         $skyblue,\n  \"alert\":         $red,\n  \"outstanding\":   $skyblue\n) !default;\n\n// 背景の色\n$bg-colors: (\n  \"primary\":        $black,\n  \"primary-tp\":     $black-tp,\n  \"secondary\":      $gray-2xh,\n  \"tertiary\":       $gray-xh,\n  \"quaternary\":     $gray-h,\n  \"quinary\":        $gray-md,\n  \"active\":         $gray-h,\n  \"loading\":        $white,\n  \"modal\":          $gray-h,\n  \"alert\":          $red,\n  \"outstanding\":    $skyblue\n) !default;\n\n// ボタンの色\n$btn-bg-colors: (\n  \"primary\":        $gray-xh,\n  \"secondary\":      $gray-l,\n  \"white-back\":     $black,\n  \"outstanding\":    $skyblue\n) !default;\n\n$border-colors: (\n  \"primary\":       $gray-xl,\n  \"secondary\":     $gray-2xl,\n  \"white-back\":    $black\n) !default;\n\n\n/*  #Font  ---------------------------------------------*/\n$font-size-base:           1rem !default;\n$font-size-2xsm:           $font-size-base * 0.62 !default;\n$font-size-xsm:            $font-size-base * 0.72 !default;\n$font-size-sm:             $font-size-base * 0.82 !default;\n$font-size-md:             $font-size-base * 1 !default;\n$font-size-lg:             $font-size-base * 1.2 !default;\n$font-size-xlg:            $font-size-base * 1.4 !default;\n$font-size-2xlg:           $font-size-base * 1.8 !default;\n$font-size-3xlg:           $font-size-base * 2.4 !default;\n$font-size-exlg:           $font-size-base * 4 !default;\n\n$font-size: (\n  \"primary\":               $font-size-sm,\n  \"secondary\":             $font-size-xsm,\n  \"tertiary\":              $font-size-2xsm,\n  \"title-primary\":         $font-size-xlg,\n  \"title-secondary\":       $font-size-lg,\n  \"title-tertiary\":        $font-size-md,\n  \"title-quaternary\":      $font-size-sm,\n  \"title-output\":          $font-size-3xlg,\n  \"link-primary\":          $font-size-md,\n  \"table-label-primary\":   $font-size-md,\n  \"table-label-secondary\": $font-size-sm,\n  \"card\":                  $font-size-md,\n  \"btn-icon\":              $font-size-sm,\n  \"btn-icon-sm\":           $font-size-xsm,\n  \"btn-icon-md\":           $font-size-md,\n  \"btn-icon-lg\":           $font-size-xlg,\n  \"btn-icon-xlg\":          $font-size-2xlg,\n  \"btn-icon-2xlg\":         $font-size-3xlg,\n  \"btn-icon-exlg\":         $font-size-exlg,\n  \"label\":                 $font-size-md,\n  \"input\":                 $font-size-sm,\n  \"msg-input\":             $font-size-md,\n  \"note\":                  $font-size-2xsm,\n  \"error\":                 $font-size-xsm,\n  \"date\":                  $font-size-xsm,\n  \"copy-right\":            $font-size-xsm,\n  \"brieftxt-sm\":           $font-size-xsm,\n  \"brieftxt-pc\":           $font-size-sm,\n  \"placeholder-secondary\": $font-size-xsm,\n) !default;\n\n/*  #Line height  ---------------------------------------------*/\n$lineheight-base:          1rem !default;\n$lineheight-half:         $lineheight-base * 1 !default;\n$lineheight-sm:         $lineheight-base * 1.25 !default;\n$lineheight-md:           $lineheight-base * 1.5 !default;\n$lineheight-double:       $lineheight-base * 2 !default;\n$lineheight-size: (\n  \"half\":                  1.5rem,\n  \"double\":                $lineheight-double,\n  \"msg-input\":             $lineheight-sm,\n) !default;\n\n\n/*  #Border  ---------------------------------------------*/\n$border-size-base:      1px !default;\n$border-size-sm:        $border-size-base * 0.5 !default;\n$border-size-md:        $border-size-base * 1 !default;\n$border-size-lg:        $border-size-base * 2 !default;\n$border-size-xlg:        $border-size-base * 4 !default;\n\n\n$border: (\n  \"primary\":            $border-size-md solid map-get($border-colors, primary),\n  \"primary-sm\":         $border-size-sm solid map-get($border-colors, primary),\n  \"secondary\":          $border-size-md solid map-get($border-colors, secondary),\n  \"secondary-sm\":       $border-size-sm solid map-get($border-colors, secondary),\n  \"white-back\":         $border-size-md solid map-get($border-colors, white-back),\n  \"notice-card\":        $border-size-xlg solid map-get($border-colors, primary),\n) !default;\n\n\n$border-radius-base:    1rem !default;\n$border-radius-xsm:      $border-radius-base * 0.3 !default;\n$border-radius-sm:      $border-radius-base * 0.5 !default;\n$border-radius-md:      $border-radius-base;\n$border-radius-lg:      $border-radius-base * 2 !default;\n\n$prsnt-border-radius-base:    10% !default;\n$prsnt-border-radius-sm:      $border-radius-base * 0.3 !default;\n$prsnt-border-radius-md:      $border-radius-base;\n$prsnt-border-radius-lg:      $border-radius-base * 2 !default;\n$prsnt-border-radius-full:    $border-radius-base * 5 !default;\n\n$border-radius-size: (\n  \"primary\":            $border-radius-xsm,\n  \"secondary\":          $border-radius-sm,\n  \"tertiary\":           $border-radius-md,\n  \"p-primary\":          $prsnt-border-radius-sm,\n  \"p-secondary\":        $prsnt-border-radius-md,\n  \"full\":               $prsnt-border-radius-full,\n\n) !default;\n\n\n\n/*  #Space (padding / margin)  ---------------------------------------------*/\n$space-base:          1rem !default;\n$space-exsm:          $space-base * 0.1 !default;\n$space-3xsm:          $space-base * 0.2 !default;\n$space-2xsm:          $space-base * 0.3 !default;\n$space-xsm:           $space-base * 0.5 !default;\n$space-sm:            $space-base * 0.7 !default;\n$space-md:            $space-base * 1 !default;\n$space-lg:            $space-base * 1.5 !default;\n$space-xlg:           $space-base * 2 !default;\n$space-2xlg:          $space-base * 3 !default;\n$space-3xlg:          $space-base * 4 !default;\n$space-auto:          auto !default;\n\n\n/*  #Layout  -----------------------------------------------------------------*/\n// Header\n$header-height:          60px !default;\n$l-header: (\n  \"height\":              $header-height,\n) !default;\n\n// Main\n$main-content-width:          85% !default;\n$l-main: (\n  \"content-width\": $main-content-width,\n) !default;\n\n// Footer\n$l-footer: (\n  \"height\":                   60px,\n  \"padding\":                  0 $space-2xlg,\n) !default;\n\n/*  #Project   ---------------------------------------------------------------*/\n// modal\n$p-modal-height-base:     100% !default;\n$p-modal-maxheight:       $p-modal-height-base * 0.88 !default;\n$p-modal-top-pos:         ($p-modal-height-base - $p-modal-maxheight) / 2;\n$p-modal: (\n  \"bg-color\":                 map-get($bg-colors, entry-modal),\n  \"modal-maxheight\":          $p-modal-maxheight,\n  \"modal-top-pos\":            $p-modal-top-pos,\n) !default;\n\n// entry\n$p-entry: (\n  \"header-font-size\":         $font-size-2xlg,\n  \"txt-font-size\":            $font-size-sm,\n) !default;\n\n$p-entry-top: (\n\n) !default;\n\n$p-entry-about: (\n  \"bg-color\":                 $gray-md,\n) !default;\n\n// index\n$p-index-sidebar: (\n  \"width\":                 245px,\n) !default;","/*--------\n|  Base  | - default/reset\n--------*/\n\n@use 'tools/global' as *;\n\n* {\n  box-sizing: border-box;\n  color: inherit;\n}\n\n// html, body {\n//   height: 100%;\n// }\n\nbody {\n  margin: auto;\n  color: map-get($font-colors, primary);\n  background-color: $black;\n  font-size: map-get($font-size, primary);\n  font-family: \"Helvetica Neue\", \"Helvetica\", Helvetica, Arial, sans-serif;\n  -webkit-font-smoothing: antialiased; \n  -webkit-text-size-adjust: none;\n}\n\na {\n  outline: none;\n  text-decoration: none;\n  color: inherit;\n  cursor: pointer;\n}\n\np { overflow-wrap: break-word; }\n\nbutton, input {\n  padding: 0;\n  border: none;\n  outline: none;\n  font: inherit;\n}\n\nbutton {\n  background-color: inherit;\n  cursor: pointer;\n}\n\nlabel { cursor: pointer; }\n\ntextarea {\n  font-family: \"Helvetica Neue\", \"Helvetica\", Helvetica, Arial, sans-serif;\n}","// _header.scss\n\n@use \"../foundation/tools/global\" as *;\n\n.l-header__submenu-toggler,\n.l-header__main-layer-entry, .l-header__sub-layer-entry {\n  display: flex;\n  align-items: center;\n  height: 46px;\n  cursor: pointer;\n  @include hover() {\n    background-color: map-get($bg-colors, tertiary);\n  }\n  @include mq(pc) {\n    padding: 0 $space-sm;\n    border-radius: map-get($border-radius-size, primary);\n  }\n}\n\n.l-header__sub-layer-entry { \n  display: flex;\n  align-items: center;\n  width: 100%;\n}\n\n#l-header {\n  align-items: center;\n  z-index: 5;\n  position: fixed;\n  top: 0;\n  left: 0;\n  width: 100%;\n  height: map-get($l-header, height);\n  background-color: map-get($bg-colors, secondary);\n}\n\n/* --------------------------------------------------------------------\n|  height, background-colorは                                          |\n|  l-header-wrapの裏からスライド表示されるl-header-menuの透過を隠す目的       |\n----------------------------------------------------------------------*/\n.l-header__wrap {\n  display: flex;\n  flex: 1;\n  flex-flow: row;\n  align-items: center;\n  z-index: 4;\n  height: 100%;\n  width: 100%;\n  padding: 0 $space-lg;\n  background-color: map-get($bg-colors, secondary);\n  @include mq(tab-max) {\n    flex: none;\n  }\n}\n\n\n/* --------------------------------------------------------------------\n|  left menu                                                           |\n----------------------------------------------------------------------*/\n\n\n.l-header__leftmenu {\n  display: flex;\n  position: relative;\n  width: 50vw;\n  margin-right: auto;\n  font-size: map-get($font-size, link-primary);\n  @include mq(tab-max) {\n    width: 80vw;\n  }\n}\n\n.l-header__home-entry, .l-header__home-entry--arrow,\n#l-header__server-banner, .l-header__channel-banner {\n  display: flex;\n  align-items: center;\n  height: 45px;\n  padding: 0 $space-sm;\n  border-radius: map-get($border-radius-size, primary);\n  @include hover() {\n    background-color: map-get($bg-colors, tertiary);\n  }\n}\n.l-header__home-entry { width: fit-content; }\n.l-header__home-entry--arrow { padding: 0; }\n\n#l-header__server-banner, .l-header__channel-banner {\n  @include mq(sp) {\n    padding: 0 $space-xsm;\n  }\n}\n\n// サーバーバナー押下での挙動が現在ないためhoverでのbg変更を相殺\n#l-header__server-banner {\n  background-color: transparent !important;\n}\n\n.l-header__home-entry--arrow {\n  position: relative;\n  display: inline-block;\n  &::before{\n    content: '';\n    position: absolute;\n    top: 57.5%;\n    right: 0;\n    left: 0;\n    transform: rotate(225deg);\n    width: 7px;\n    height: 7px;\n    margin: auto;\n    margin-top: -0.5rem;\n    border-right: solid 1.5px map-get($font-colors, primary);\n    border-top: solid 1.5px map-get($font-colors, primary);\n  }\n}\n\n// #l-header__server-banner {}\n\n.l-header__server-img {\n  width: 30px;\n  height: 30px;\n  margin-right: $space-xsm;\n  border-radius: map-get($border-radius-size, primary);\n  object-fit: cover;\n}\n\n.l-header__server-name { max-width: 350px; }\n\n.l-header__server-name, .l-header__channel-name {\n  @include mq(sp) {\n    max-width: 125px;\n  }\n}\n\n.l-header__channel-banner {\n  // margin-left: $space-sm;\n  background-color: map-get($bg-colors, primary);\n}\n\n.l-header__channel-name { margin-left: $space-xsm }\n\n.l-header__channel-list {\n  display: none;\n  position: absolute;\n  top: 45px;\n  right: 0;\n  min-width: 250px;\n  max-width: 300px;\n  padding: $space-2xsm;\n  border: map-get($border, primary);\n  border-radius: map-get($border-radius-size, primary);\n  background-color: map-get($bg-colors, primary);\n  &[data-status='on'] {\n    display: block;\n  }\n}\n\nbutton#p-modal-opener--new-channel{\n  @include hover {\n    background-color: map-get($bg-colors, tertiary);\n  }\n}\n\n\n/* --------------------------------------------------------------------\n|  main menu                                                           |\n----------------------------------------------------------------------*/\n\n\n#l-header__mainmenu-toggler {\n  position: relative;\n}\n\n#l-header__mainmenu-opener {\n  transform: scale(1);\n  position: absolute;\n  top: -17px;\n  right: 0;\n  border: none;\n  font-size: map-get($font-size, btn-icon);\n  > i {\n    margin: auto 0;\n  }\n  &[data-status='on'] {\n    transition: transform 0.15s 0s ease;\n    transform: scale(0);\n  }\n  &[data-status='off'] {\n    transition: transform 0.15s 0.15s ease;\n    transform: scale(1);\n  }\n}\n\n#l-header__mainmenu-closer {\n  transform: scale(0);\n  position: absolute;\n  top: -17px;\n  right: 0;\n  &[data-status='on'] {\n    transition: transform 0.15s 0.15s ease;\n    transform: scale(1);\n  }\n  &[data-status='off'] {\n    transition: transform 0.15s 0s ease;\n    transform: scale(0);\n  }\n}\n\n#l-header__mainmenu {\n  transition: transform 0.3s 0s ease;\n  display: flex;\n  flex-flow: row;\n  align-items: center;\n  z-index: 4;\n  padding: 0 $space-xlg;\n  background-color: map-get($bg-colors, secondary);\n  @include mq(tab-max) {\n    flex-flow: column;\n    align-items: initial;\n    z-index: 3;\n    position: absolute;\n    bottom: 1.5px;\n    width: 100%;\n  }\n  &[data-status='on'] {\n    transform: translateY(100%);\n  }\n  &[data-status='off'] {\n    transform: translateY(-60px);\n  }\n}\n\n.l-header__mainmenu-item {\n  position: relative;\n  @include hover(pc) {\n    .l-header__submenu {\n      display: block;\n    }\n  }\n}\n\n.l-header__submenu {\n  display: none;\n  @include mq(pc) {\n    position: absolute;\n    top: 38px;\n    right: 0;\n    width: max-content;\n    min-width: 220px;\n    max-width: 250px;\n    padding: $space-2xsm;\n    border: map-get($border, primary);\n    border-radius: map-get($border-radius-size, primary);\n    background-color: map-get($bg-colors, secondary);\n  }\n  @include mq(tab-max) {\n    &[data-status='on'] {\n      display: block;\n    }\n    &[data-status='off'] {\n      display: none;\n    }\n  }\n}\n\n.l-header__submenu-toggler {\n  display: flex;\n  justify-content: space-between;\n  width: 100%;\n  cursor: pointer;\n}\n\n.l-header__submenu-arrow {\n  position: relative;\n  display: inline-block;\n  &::before{\n    content: '';\n    position: absolute;\n    top: 50%;\n    right: 0.3rem;\n    transform: rotate(135deg);\n    width: 7px;\n    height: 7px;\n    margin-top: -0.5rem;\n    border-right: solid 1.5px map-get($font-colors, primary);\n    border-top: solid 1.5px map-get($font-colors, primary);\n  }\n}\n\n.l-header__submenu-pilar-icon { //? pilar icon componentならん？ (p-index-aboutにあり)\n  height: 2rem;\n  border-left: 4px solid white;\n  margin: auto $space-md auto 0;\n}\n\n.l-header__submenu-item-title {\n  font-size: map-get($font-size, title-quaternary);\n}\n\n.l-header__submenu-item-detail {\n  font-size: map-get($font-size, note);\n  margin-top: $space-xsm;\n  word-wrap: break-word;\n}\n\n.l-header__flash-wrap {\n  display: flex;\n  position: fixed;\n  top: map-get($l-header, height);\n}\n\n.l-header__flash-message {\n  display: flex;\n  justify-content: space-between;\n  width: 100vw;\n  padding: $space-2xsm $space-xlg;\n  background-color: map-get($bg-colors, secondary);\n}","// _mixin.scss\n\n@use \"sass:meta\";\n\n// メディアクエリ ---------------------------------------------------------\n$breakpoints: (\n  sp:         'screen and (max-width: 767px)',\n  tab-max:    'screen and (max-width: 1024px)',\n  tab:        'screen and (min-width: 768px) and (max-width: 1024px)',\n  tab-min:    'screen and (min-width: 768px)',\n  pc:         'screen and (min-width: 1025px)'\n);\n\n@mixin mq($bp) {\n  @media #{map-get($breakpoints, $bp)} {\n    @content;\n  }\n}\n\n// 対象画面を絞るmin-width (al->allの意, spは指定なし)\n$frame-min-widths: (\n  al:    'min-width: 0px',\n  tab:   'min-width: 768px',\n  pc:    'min-width: 1025px'\n);\n\n// Hoverを設定 (対象デバイスがhover可能およびポインターデバイスを使用している場合に機能します)\n@mixin hover ($frame: null) {\n  $frame-min-width: \"\";\n  @if $frame == pc {\n    $frame-min-width: map-get($frame-min-widths, pc);\n  } @else if $frame == tab {\n    $frame-min-width: map-get($frame-min-widths, tab);\n  } @else {\n    $frame-min-width: map-get($frame-min-widths, al);\n  }\n  @media (hover: hover) and (pointer: fine) and ($frame-min-width) {\n    &:hover {\n      @content;\n    }\n  }\n}\n\n// ポインターデバイスを使用している場合に機能します\n@mixin pointer () {\n  @media (pointer: fine) {\n    @content;\n  }\n}\n\n// ポインターデバイスを使用していない場合に機能します\n@mixin non-pointer () {\n  @media (pointer: none) {\n    @content;\n  }\n}\n\n// Link(aタグ etc.)用のサイズ適正化\n@mixin optimized-link {\n  display: flex;\n  align-items: center;\n}","// _main.scss\n\n@use \"../foundation/tools/global\" as *;\n\n.l-main {\n  padding-top: map-get($l-header, height);\n}\n\n.l-main--app {\n  @extend .l-main;\n  display: flex;\n  width: 100%;\n}\n\n.l-main__section, .l-main__app-section, .l-main__fixed-section {\n  display: flex;\n  min-height: calc(100vh - map-get($l-header, height));\n  width: 100vw;\n  > .content, .wide-content, .app-content {\n    height: fit-content;\n    width: map-get($l-main, content-width);\n    padding: $space-xlg 0;\n  }\n  > .content, .wide-content {\n    margin: auto;\n    @include mq(tab-min) { padding: $space-2xlg 0; }\n  }\n  > .content, .app-content { max-width: map-get($l-main, 1000px); }\n  > .wide-content { max-width: map-get($l-main, 1200px); }\n  > .app-content {\n    margin: 0 auto;\n  }\n}\n\n.l-main__fixed-section {\n  position: fixed;\n  top: 0;\n  z-index: 6;\n  height: 100%;\n  width: 100%;\n  background-color: map-get($bg-colors, primary);\n  &:not([data-status='on']) {\n    display: none;\n  }\n}\n.l-main__fixed-section-closer {\n  position: absolute;\n  top: $space-3xlg;\n  right: $space-2xlg;\n  font-size: map-get($font-size, btn-icon-2xlg);\n}\n\n.l-main__section:last-child {\n  min-height: calc(100vh - (map-get($l-header, height) + (map-get($l-footer, height))));\n}\n\n.l-main:nth-child(1) { // headerが存在しない場合に画角を調節する\n  padding: 0;\n  .l-main__section { min-height: calc(100vh - (map-get($l-footer, height))); }\n}\n\n.l-main__section-header {\n  text-align: center;\n  margin-bottom: $space-lg;\n  font-size: map-get($p-entry, header-font-size);\n  @include mq(sp) {\n    font-size: map-get($font-size, title-secondary);\n  }\n}\n\n.l-main__section-brief {\n  text-align: center;\n  margin-bottom: $space-xlg;\n  font-size: map-get($font-size, brieftxt-pc);\n  line-height: map-get($lineheight-size, double);\n  @include mq(sp) {\n    font-size: map-get($font-size, brieftxt-sm);\n  }\n}\n\n.l-main__link-section {\n  display: flex;\n  justify-content: space-between;\n  width: 100%;\n  margin-bottom: 1.5rem;\n  font-size: 1rem;\n}\n\n.l-main__link-section-text { margin: 0 $space-2xsm; }","// _footer.scss\n\n@use \"../foundation/tools/global\" as *;\n\n.l-footer {\n  height: map-get($l-footer, height);\n  padding: map-get($l-footer, padding);\n  line-height: map-get($l-footer, height);\n}\n\n.l-footer__copy-light-txt {\n  font-size: map-get($font-size, copy-right);\n}","// _button.scss\n\n@use '../../foundation/tools/global' as *;\n\n// ボタンのベースです\n.c-btn {\n  display: flex;\n  justify-content: space-around;\n  align-items: center;\n  padding: 0 $space-2xsm;\n  color: inherit;\n  cursor: pointer;\n  > i { // FontAwesome経由のiconを中央に\n    margin: auto;\n  }\n}\n\n// background-color付きのボタンです\n@each $key, $value in $btn-bg-colors {\n  .c-btn--bg-#{$key} {\n    background-color: $value;\n  }\n  .c-btn--hover-bg-#{$key} {\n    border: map-get($border, \"#{$key}\");\n    @include hover() {\n      background-color: $value;\n      @if $key == \"white-back\" {\n        color: map-get($font-colors, primary);\n      }\n    }\n  }\n}\n\n// 四角形のボタンです\n.c-btn--quad {\n  @extend .c-btn;\n  min-height: 34px;\n  min-width: 34px;\n  border-radius: map-get($border-radius-size, primary);\n}\n\n.c-btn--xquad {\n  @extend .c-btn--quad, .c-btn--hover-bg-white-back;\n}\n\n.c-btn--submit {\n  @extend .c-btn--quad, .c-btn--bg-primary, .c-btn--hover-bg-secondary;\n  padding: 0 4rem;\n}\n\n.c-btn--submit-full {\n  @extend .c-btn--quad, .c-btn--bg-primary, .c-btn--hover-bg-secondary;\n  width: 100%;\n}\n\n.c-btn--min-rect {\n  @extend .c-btn--quad;\n  min-width: 65px;\n  background-color: map-get($bg-colors, tertiary);\n  @include hover {\n    background-color: map-get($btn-bg-colors, secondary);\n  }\n}\n\n.c-btn--outstanding-rect {\n  @extend .c-btn--quad;\n  padding: $space-xsm $space-sm;\n  background-color: map-get($btn-bg-colors, outstanding);\n  @include hover {\n    background-color: lighten(map-get($btn-bg-colors, outstanding), 10%);\n  }\n}","// _card.scss\n\n@use '../../foundation/tools/global' as *;\n\n.card {\n  width: fit-content;\n  padding: $space-md $space-lg;\n  border-left: map-get($border, notice-card);\n  border-radius: map-get($border-radius-size, primary);\n  font-size: map-get($font-size, card);\n  line-height: map-get($lineheight-size, double);\n  background-color: map-get($bg-colors, secondary);\n}","// _wrapper.scss\n\n@use '../../foundation/tools/global' as *;\n\n.c-flex-wrapper { display: flex; }\n\n.c-flex-wrapper--center {\n  display: flex;\n  align-items: center;\n}\n\n.c-flex-wrapper--column {\n  display: flex;\n  flex-flow: column;\n}\n\n.c-flex-wrapper--row {\n  display: flex;\n  flex-flow: row;\n}","// _menu.scss\n\n@use '../../foundation/tools/global' as *;\n\n.c-menu-wrap {\n  overflow: hidden;\n  position: relative;\n}\n\n.c-menu-wrap > .c-corner-icon-menu {\n  display: flex;\n  overflow: hidden;\n  position: absolute;\n  top: -0.3rem;\n  right: -0.3rem;\n  padding: 0.3rem 0.3rem 0 0;\n  border-radius: 0.3rem;\n  background-color: map-get($bg-colors, secondary);\n  > .c-menu-elm {\n    display: flex;\n    justify-content: space-around;\n    align-items: center;\n    position: relative;\n    width: 32px;\n    height: 30px;\n    &:not(:last-child)::after {\n      content: \"\";\n      position: absolute;\n      right: 0;\n      height: 15px;\n      border-right: map-get($border, primary);\n    }\n  }\n}","// _partition.scss\n\n@use '../../foundation/tools/global' as *;\n\n.c-patition { border-top: map-get($border, secondary); }\n\n.c-partition--full {\n  @extend .c-patition;\n  width: 100%;\n  margin: $space-md auto;\n}\n\n.c-partition--modal {\n  @extend .c-patition;\n  width: 65%;\n  min-width: 250px;\n  margin: $space-lg auto;\n}","// _auth.scss\n\n@use '../../foundation/tools/global' as *;\n\n.p-form__wrap {\n  width: 50%;\n  min-width: 250px;\n  margin: 0 auto;\n}\n\n.p-form-wrap--min {\n  @extend .p-form__wrap;\n  width: 100%;\n  min-width: auto;\n}\n\n.p-form__input-label {\n  display: inline-block;\n  margin-bottom: $space-2xsm;\n  font-weight: bold;\n}\n\n.p-form__image-label {\n  display: block;\n  width: fit-content;\n  margin: $space-2xsm 0 $space-md;\n}\n.p-form__profile-image-label {\n  overflow: hidden;\n  position: relative;\n  height: 150px;\n  width: 150px;\n  margin: auto;\n  background-color: map-get($bg-colors, tertiary);\n  border-radius: map-get($border-radius-size, full);\n  font-size: map-get($font-size, btn-icon-exlg);\n  @include mq(sp) {\n    height: 150px;\n    width: 150px;\n  }\n  > i { position: absolute; }\n  > img {\n    object-fit: cover;\n    height: inherit;\n    width: inherit;\n    z-index: 5;\n  }\n}\n\n.p-form__image-label:has(+ .p-form__server-image:disabled) {\n  opacity: 0.5;\n}\n\n.p-form__input {\n  width: 100%;\n  margin-bottom: $space-lg;\n  padding: $space-sm;\n  border: map-get($border, primary);\n  border-radius: map-get($border-radius-size, primary);\n  font-size: map-get($font-size, input);\n  background-color: map-get($bg-colors, primary);\n}\n\n.p-form__error-msg {\n  float: right;\n  margin-left: $space-md;\n  font-size: map-get($font-size, error);\n  color: map-get($font-colors, alert);\n}\n\n.p-form__psw-forget-note {\n  text-align: center;\n  margin-bottom: $space-md;\n  font-size: map-get($font-size, note);\n}\n\n.p-form__submit {\n  margin: $space-md auto 0;\n  border: none;\n}\n\n#p-form__password-visualizer {\n  i:last-child { display: none; }\n}\n\n.p-form__checkbox-wrap {\n  display: flex;\n  justify-content: space-evenly;\n}\n\n.p-form__checkbox-label {\n  padding: $space-2xsm $space-sm;\n  border-radius: map-get($border-radius-size, primary);\n  background-color: map-get($btn-bg-colors, primary);\n}\n\n.p-form__checkbox { display: none; }\n\n.p-form__checkbox:checked + .p-form__checkbox-label {\n  background-color: map-get($btn-bg-colors, secondary);\n}\n\n.p-form__input:disabled {\n  text-decoration-line: line-through;\n  background-color: map-get($bg-colors, secondary);\n}\n\n.p-form__file-block {\n  position: relative;\n  background-color: #222;\n  width: 175px;\n  padding: $space-sm $space-2xsm;\n  border-radius: map-get($border-radius-size, primary);\n  @include mq(sp) {\n    width: 90px;\n    padding: $space-2xsm\n  }\n}\n\n.p-form__image-wrap {\n  display: flex;\n  z-index: 0;\n  position: relative;\n  height: 160px;\n  margin-bottom: $space-sm;\n  border-radius: map-get($border-radius-size, primary);\n  font-size: map-get($font-size, btn-icon-exlg);\n  background-color: map-get($bg-colors, quinary);\n  > i {\n    position: absolute;\n    top: calc(50% - map-get($font-size, btn-icon-exlg) / 2);\n    left: calc(50% - map-get($font-size, btn-icon-exlg) / 2);\n  }\n  > img {\n    z-index: 5;\n    height: 100%;\n    width: 100%;\n    object-fit: cover;\n  }\n  @include mq(sp) {\n    height: 80px;\n    margin-bottom: $space-2xsm\n  }\n}\n\n.p-form__filename {\n  overflow: hidden;\n  width: 100%;\n  font-size: map-get($font-size, secondary);\n  text-overflow: ellipsis;\n  white-space: nowrap;\n  @include mq(sp) {\n    font-size: map-get($font-size, tertiary);\n  }\n}\n\n\n\n.p-form__msg-input {\n  background-color: map-get($bg-colors, tertiary);\n  border-radius: map-get($border-radius-size, secondary);\n}\n\n.p-form__circle-preview-showcase {\n  position: relative;\n  width: fit-content;\n  margin: auto;\n  > .p-form__clear-circle-preview {\n    position: absolute;\n    top: 0;\n    right: -$space-lg;\n    font-size: map-get($font-size, btn-icon-lg);\n  }\n}\n\n.p-form__preview-showcase {\n  overflow: scroll;\n  margin: 0 $space-sm;\n  padding: $space-md $space-sm;\n  border-bottom: map-get($border, primary-sm);\n}\n\nul.p-form__preview-list {\n  display: flex;\n  width: fit-content;\n  > li:not(:last-child) {\n    margin-right: $space-md;\n    @include mq(sp) {\n      margin-right: $space-sm;\n    }\n  }\n}\n\n.p-form__file-thumnail {\n  overflow: hidden;\n  border-radius: 1rem;\n  background-color: map-get($bg-colors, primary);\n  width: fit-content;\n}\n\n.p-form__msg-input-bar { padding: $space-xsm $space-xlg $space-xsm 0; }\n\n.p-form__file-opener-wrap {\n  display: flex;\n  justify-content: space-around;\n  align-items: center;\n}\n\n#p-form__file-opener {\n  height: 1.5rem;\n  width: 1.5rem;\n  background-color: map-get($bg-colors, quinary);\n  border-radius: map-get($border-radius-size, full);\n}\n\n.p-form__msg-txtarea {\n  border: none;\n  outline: none;\n  resize: none;\n  // ▼ padding上下 + line-height*(行数-1) + font-size\n  max-height: calc($space-xsm * 2 + map-get($lineheight-size, msg-input) * 9 + map-get($font-size, msg-input));\n  padding: $space-xsm $space-xsm $space-xsm $space-sm;\n  border-radius: map-get($border-radius-size, primary);\n  background-color: map-get($bg-colors, secondary);\n  font-size: map-get($font-size, msg-input);\n  line-height: map-get($lineheight-size, msg-input);\n  @include mq(sp) {\n    max-height: calc($space-xsm * 2 + map-get($lineheight-size, msg-input) * 7 + map-get($font-size, msg-input));\n  }\n  &:not(:has(+ input)) {\n    min-width: 40vw;\n    @include mq(tab) { min-width: 50vw; }\n    @include mq(sp) { min-width: 65vw; }\n  }\n}\n.p-form__msg-txtarea-closer {\n  width: fit-content;\n  border-bottom: map-get($border, primary-sm);\n}\n\n.p-form__file-dumper, #p-form__file-opener {\n  > i { transition: all 0.15s; }\n  @include hover {\n    > i { scale: 1.2; }\n  }\n}\n\n.p-form__search-bar-wrap {\n  position: relative;\n  max-width: 500px;\n  margin: $space-lg;\n}\n.p-form__search-bar {\n  margin: 0;\n  padding-right: 150px;\n  @include mq(tab-max) { padding-right: 50px; }\n}\n.p-form__search-bar-status {\n  position: absolute;\n  top: 0;\n  right: $space-sm;\n  bottom: 0;\n  display: flex;\n  align-items: center;\n  .status-text, i { opacity: 0.4; }\n}\n#p-form__search-bar-status--blank {\n  i { font-size: map-get($font-size, btn-icon-md); }\n}\n#p-form__search-bar-status--filled {\n  display: contents;\n  .status-text {\n    margin: $space-2xsm;\n    font-size: map-get($font-size, placeholder-secondary);\n    cursor: default;\n  }\n  i { \n    font-size: map-get($font-size, btn-icon-lg); \n    @include hover { opacity: 1; }\n  }\n}\n\n.p-form__search-bar-result {\n  width: 70%;\n  @include mq(tab) { min-width: 500px; }\n  @include mq(sp) { min-width: 300px; }\n}\n.annotation:has(+.p-form__server-banner-list) { \n  margin-bottom: $space-sm; \n}\n.p-form__server-banner-list {\n  overflow: scroll;\n  max-height: 470px;\n  width: 100%;\n  > li {\n    margin-bottom: $space-sm;\n    &:first-child { @include pointer { margin-top: $space-sm; } }\n    &:last-child { @include non-pointer { margin: 0; } }\n  }\n}\n.p-form__server-banner {\n  display: flex;\n  @include pointer { padding-left: $space-xsm; }\n  @include hover {\n    > .p-form__searched-server-img-wrap { scale: 1.05; }\n  }\n}\n.p-form__searched-server-img-wrap {\n  transition: all 0.15s;\n  overflow: hidden;\n  width: 180px;\n  height: 110px;\n  margin-right: $space-sm;\n  border-radius: map-get($border-radius-size, primary);\n  &:not(:has(img)) { background-color: map-get($bg-colors, secondary); }\n  > img {\n    height: inherit;\n    width: inherit;\n    object-fit: cover;\n  }\n  @include mq(tab) {\n    width: 145px;\n    height: 85px;\n  } \n  @include mq(sp) {\n    width: 100px;\n    height: 65px;\n  }\n}\n.p-form__searched-server-info {\n  display: flex;\n  flex-flow: column;\n  justify-content: space-between;\n  overflow: hidden;\n  .server-name {\n    overflow: hidden;\n    display: -webkit-box;\n    font-size: map-get($font-size, title-secondary);\n    font-weight: bold;\n    -webkit-box-orient: vertical;\n    -webkit-line-clamp: 3;\n    @include mq(tab-max) {\n      font-size: map-get($font-size, title-tertiary);\n    }\n    @include mq(sp) { -webkit-line-clamp: 2;}\n  }\n  .server-population {\n    @include mq(tab-max) { font-size: map-get($font-size, secondary); }\n  }\n}","// _modal.scss\n\n@use '../../foundation/tools/global' as *;\n\n// jsのフックとしての役割あり\n.p-modal {\n  z-index: 7;\n  opacity: 0;\n  transform: scaleY(0);\n  transition: transform 0.2s ease-out, opacity 0.08s ease-in;\n  position: fixed;\n  top: map-get($p-modal, modal-top-pos);\n  right: 0;\n  left: 0;\n  width: 75%;\n  margin: auto;\n  padding: $space-lg $space-md;\n  border-radius: map-get($border-radius-size, primary);\n  background-color: map-get($bg-colors, modal);\n  &[data-status='on'] {\n    opacity: 1;\n    transform: scaleY(1);\n  }\n  &[data-status='off'] {\n    opacity: 0;\n    transform: scaleY(0);\n  }\n}\n\n.p-modal--min {\n  top: 50%;\n  max-width: 420px;\n  &[data-status='on'] {\n    transform: translateY(-50%);\n  }\n}\n\n.p-modal-closer {\n  position: absolute;\n  top: 5px;\n  right: 5px;\n}\n\n.p-modal-wrap{\n  overflow: scroll;\n  max-height: 86vh;\n  // padding: 4% 1%;\n}\n\n.p-modal-content{\n  justify-content: space-around;\n  margin: 0;\n}\n\n// .p-modal-block { padding: $space-sm; }\n\n.p-modal-header {\n  text-align: center;\n  font-size: map-get($font-size, title-primary);\n  font-weight: bold;\n  margin-bottom: $space-md;\n}\n\n.p-modal-section { min-width: 250px; }\n\n.p-modal-section {\n  width: 50%;\n  margin: 0 auto;\n}","// _slide.scss\n\n@use '../../foundation/tools/global' as *;\n\n.p-slide-wrap {\n  z-index: 7;\n  position: fixed;\n  inset: 0;\n  height: fit-content;\n  width: fit-content;\n  margin: auto;\n}\n\n.p-slide-closer {\n  position: absolute;\n  top: 0;\n  right: $space-sm;\n}\n\n\n// Splide jsから生成されたスライドの装飾\n\n.splide {\n  width: 95vw;\n}\n\n.splide__track {\n  overflow: auto;\n}\n\n.splide__slide {\n  display: flex;\n  justify-content: space-around;\n}\n\n.splide__slide img {\n  object-fit: contain;\n  max-height: 75vh;\n  max-width: 70vw;\n}\n\n.splide__pagination { bottom: -$space-lg; }","// _management.scss\n\n@use '../../foundation/tools/global' as *;\n\n.p-management__section-brief { font-size: map-get($font-size, title-tertiary); }\n\n.p-management__code {\n  background: map-get($bg-colors, secondary);\n  border-radius: map-get($border-radius-size, primary);\n  padding: 0 $space-2xsm;\n}\n\n.p-management__notice-wrap {\n  width: fit-content;\n  margin: 0 auto;\n  padding: $space-md $space-lg;\n  border-left: map-get($border, notice-card);\n  border-radius: map-get($border-radius-size, primary);\n  font-size: map-get($font-size, card);\n  line-height: map-get($lineheight-size, double);\n  background-color: map-get($bg-colors, secondary);\n}","// _shared.scss\n\n@use '../../foundation/tools/global' as *;\n\n// モーダル起動時用のスタイルです ----------------------------------------\n#body {\n  overflow: visible;\n  &[data-status='on'] {\n    overflow: hidden;\n  }\n}\n\n#overlay {\n  display: none;\n  opacity: 0.9;\n  position: fixed;\n  top: 0;\n  z-index: 6;\n  height: 100%;\n  width: 100%;\n  background-color: $black;\n  &[data-status='on'] {\n    display: block;\n  }\n}\n\n#loading {\n  position: fixed;\n  top: 0;\n  left: 0;\n  z-index: 7;\n  width: 100vw;\n  height: 100vh;\n  transition: all 1s;\n  background-color: map-get($bg-colors, primary);\n}\n\n.loader,\n.loader:before,\n.loader:after {\n  background: #ffffff;\n  -webkit-animation: load1 1s infinite ease-in-out;\n  animation: load1 1s infinite ease-in-out;\n  width: 1em;\n  height: 4em;\n}\n.loader {\n  color: #ffffff;\n  text-indent: -9999em;\n  margin: calc(40vh) auto;\n  position: relative;\n  font-size: 11px;\n  -webkit-transform: translateZ(0);\n  -ms-transform: translateZ(0);\n  transform: translateZ(0);\n  -webkit-animation-delay: -0.16s;\n  animation-delay: -0.16s;\n}\n.loader:before,\n.loader:after {\n  position: absolute;\n  top: 0;\n  content: '';\n}\n.loader:before {\n  left: -1.5em;\n  -webkit-animation-delay: -0.32s;\n  animation-delay: -0.32s;\n}\n.loader:after {\n  left: 1.5em;\n}\n@-webkit-keyframes load1 {\n  0%,\n  80%,\n  100% {\n    box-shadow: 0 0;\n    height: 4em;\n  }\n  40% {\n    box-shadow: 0 -2em;\n    height: 5em;\n  }\n}\n@keyframes load1 {\n  0%,\n  80%,\n  100% {\n    box-shadow: 0 0;\n    height: 4em;\n  }\n  40% {\n    box-shadow: 0 -2em;\n    height: 5em;\n  }\n}\n\n.loaded {\n  opacity: 0;\n  visibility: hidden;\n}\n\n\n\n// ビデオ通話statusの Connecting を装飾します -----------------------------------------\n\nspan.connecting {\n  position: relative;\n  display: inline-block;\n  color: map-get($font-colors, outstanding);\n}\n  span.connecting span {\n    position: absolute;\n    display: inline-block;\n    left: 0;\n    right: 0;\n    top: 120%;\n    height: 2px;\n\n    background-position: 0 0;\n    background-repeat: repeat-x;\n    background-size: 10px 100%;\n    background-image: linear-gradient(to right,transparent 5px, map-get($font-colors, outstanding) 5px, map-get($font-colors, outstanding) 10px);\n    animation: connecting 0.7s linear infinite;\n  }\n\n@-webkit-keyframes connecting {\n  0% {\n    background-position: 0 0;\n  }\n  100% {\n    background-position: 10px 0;\n  }\n}\n\n@keyframes connecting {\n  0% {\n    background-position: 0 0;\n  }\n  100% {\n    background-position: 10px 0;\n  }\n}\n\n\n\n// ビデオ通話statusの Connected を装飾します -----------------------------------------\n\nspan.connected {\n  position: relative;\n  display: inline-block;\n  color: map-get($font-colors, outstanding);\n}\n  span.connected span {\n    position: absolute;\n    display: inline-block;\n    left: 0;\n    top: 120%;\n    width: 100%;\n    height: 2px;\n\n    background-position: 0 0;\n    background-image: linear-gradient(to right, map-get($font-colors, outstanding), transparent);\n    animation: connected 2.5s linear infinite;\n  }\n\n@-webkit-keyframes connected {\n  0% {\n    width: 0;\n    background-color: transparent;\n  }\n  40% {\n    width: 100%;\n    background-color: transparent;\n  }\n  80% {\n    width: 100%;\n    background-color: $white;\n  }\n  100% {\n    width: 100%;\n    background-color: $white;\n  }\n}\n\n@keyframes connected {\n  0% {\n    width: 0;\n    background-color: transparent;\n  }\n  40% {\n    width: 100%;\n    background-color: transparent;\n  }\n  80% {\n    width: 100%;\n    background-color: $white;\n  }\n  100% {\n    width: 100%;\n    background-color: $white;\n  }\n}","// _user.scss\n\n@use '../../../foundation/tools/global' as *;\n\n// User List ---------------------------------------------\n\n.p-user-list__head-wrap {\n  text-align: center;\n  margin-bottom: $space-xsm;\n}\n\n.p-user-list__head {\n  font-weight: bold;\n  font-size: map-get($font-size, table-label-primary);\n}\n\n.p-user-list__user-info-list {\n  align-items: center;\n  margin-bottom: $space-xsm;\n  border-radius: map-get($border-radius-size, primary);\n  background-color: map-get($bg-colors, secondary)\n}\n\n.p-user-list__user-info-wrap {\n  display: flex;\n  padding: $space-2xsm $space-sm;\n  @include mq(sp) {\n    padding: 0 $space-sm $space-2xsm ;\n    &:first-child {\n      padding-top: $space-2xsm;\n    }\n  }\n}\n\n.p-user-list__user-info { margin: 0 auto; }\n\n.p-user-list__user-info--name {\n  margin: 0 auto;\n  @include hover() {\n    text-decoration-line: underline;\n  }\n}\n\n.p-user-list__user-delete-btn {\n  margin: 0 auto;\n  padding: $space-2xsm $space-sm;\n  border-radius: map-get($border-radius-size, primary);\n  background-color: map-get($btn-bg-colors, white-back);\n  @include hover {\n    background-color: map-get($btn-bg-colors, secondary);\n  }\n}","// _entry.scss\n\n@use '../../../foundation/tools/global' as *;\n\n\n\n// Top セクション -----------------------------------------------\n\n.p-entry-top__entry {\n  display: flex;\n  justify-content: space-between;\n  align-items: center;\n  margin-bottom: $space-md;\n  padding: $space-md;\n  border-radius: map-get($border-radius-size, primary);\n}\n\n.p-entry-top__entry-wrap, .p-entry-top__image-wrap { padding: 0 0.5rem; }\n\n.p-entry-top__entry-wrap { margin-bottom: $space-lg; }\n\n.p-entry-top__signin-entry { background-color: map-get($bg-colors, secondary); }\n\n.p-entry-top__signup-entry { background-color: map-get($bg-colors, tertiary); }\n\n\n\n// About セクション ---------------------------------------------\n\n// 現状なし\n\n\n\n// Func セクション ----------------------------------------------\n\n.p-entry-func__modal-entry {\n  margin-bottom: $space-xlg;\n  padding: 0 $space-xsm;\n}\n\n.p-entry-func__modal-entry:nth-child(5) { margin: 0; }\n\n.p-entry-func__thumnail-wrap {\n  transition: scale 0.2s ease;\n  display: flex;\n  justify-content: center;\n  align-items: center;\n  position: relative;\n  aspect-ratio: 5 / 3;\n  margin-bottom: $space-md;\n  border-radius: map-get($border-radius-size, primary);\n  box-shadow: 0.05em 0.15em 0.3em rgba(0, 0, 0, 0.2);\n  background-color: map-get($bg-colors, secondary);\n  font-size: map-get($font-size, btn-icon-exlg);\n  @include hover() {\n    scale: 1.05;\n    box-shadow: 0.15em 0.45em 0.9em rgba(0,0,0,0.25);\n  }\n}\n\n.p-entry-func__img-txt { text-align: center; }\n\n.p-entry-func__modal-txt {\n  overflow: scroll;\n  max-height: 40em;\n  margin-bottom: $space-md;\n  @include mq(sp) {\n    overflow: unset;\n    max-height: none;\n  }\n}\n\n.p-entry-func__modal-thumnails {\n  text-align: center;\n  >img {\n    width: 75px;\n    height: 75px;\n    margin-right: $space-2xsm;\n    margin-bottom: $space-xsm;\n    border-radius: map-get($border-radius-size, primary);\n    object-fit: cover;\n    cursor: pointer;\n    &[data-status='on'] {\n      scale: 1.1;\n      box-shadow: 0.05em 0.15em 0.3em rgba(0, 0, 0, 0.3);\n    }\n  }\n}\n\n.p-entry-func__modal-mainimage {\n  display: flex;\n  overflow: hidden;\n  >img {\n    max-height: 600px;\n    max-width: 100%;\n  }\n}","@use '../../../foundation/tools/global' as *;\n\n\n// Server --------------------------------------------------------\n.p-index__server-board {\n  margin-bottom: $space-lg;\n  padding: 0 $space-md;\n}\n\n.p-index__server-thumnail-wrap {\n  transition: scale 0.2s ease;\n  overflow: hidden;\n  position: relative;\n  margin-bottom: $space-2xsm;\n  padding-top: 95%;\n  border-radius: map-get($border-radius-size, p-secondary);\n  @include hover() {\n    scale: 1.05;\n  }\n}\n\n.p-index__server-thumnail {\n  display: flex;\n  justify-content: space-around;\n  align-items: center;\n  position: absolute;\n  top: 0;\n  right: 0;\n  bottom: 0;\n  left: 0;\n  background-color: map-get($bg-colors, tertiary);\n  > i {\n    font-size: map-get($font-size, btn-icon-xlg);\n  }\n  > img {\n    height: 100%;\n    width: 100%;\n    object-fit: cover;\n  }\n}\n\n.p-index__server-edit-btn {\n  position: absolute;\n  top: -0.5rem;\n  right: -0.3rem;\n  padding: 1rem 1rem 0.5rem 0.7rem;\n  border-radius: 0.5rem;\n  background-color: map-get($bg-colors, secondary);\n  @include mq(tab-max) {\n    display: block;\n  }\n  @include hover {\n    > i {\n      scale: 1.4;\n    }\n  }\n  > i {\n    transition: scale 0.2s ease;\n    scale: 1;\n  }\n}\n\n.p-index__server-title {\n  text-align: center;\n  font-size: map-get($font-size, title-tertiary);\n}\n\n\n\n\n// in_server --------------------------------------------------------\n\n.p-index__in-server {\n  display: flex;\n  position: fixed;\n  height: calc(100vh - map-get($l-header, height));\n  width: 100vw;\n}\n\n\n\n// side bar\n\n.p-index__sidebar {\n  position: rela