/* banner.js */

// bannerの
const HEADER_CHANNEL_BANNER = get_element('.l-header__channel-banner')[0]
const CHANNEL_BANNER_ICON = get_element('i', HEADER_CHANNEL_BANNER)[0]

const set_banner_events = () => {
    HEADER_CHANNEL_BANNER.addEventListener('click', () => {
        CHANNEL_BANNER_ICON.classList.toggle('fa-chevron-down');
        CHANNEL_BANNER_ICON.classList.toggle('fa-chevron-up');
    })
}

if (typeof HEADER_CHANNEL_BANNER != 'undefined') set_banner_events();