// ajax.js

/*------------------------
|   全体で使用する HELPER   |
------------------------*/

// フォーム内要素のdisabledを切り替えます
// jquery要素からmapで抽出した子要素は、js要素の扱いになる
// => .prop('disabled') ×   .disabled ◯
toggle_form_disabled = (form) => {
    let inputs = form.find('input');
    inputs.map((i, input) => {
        input.disabled = (input.disabled) ? false : true;
    })
}



/*-------------
|   #サーバー   |
-------------*/

// #サーバー追加用 --------------------------------------------------------------

execute_server_addition = (server_name, base64='', file_name='') => {
    $.ajax ({
        data : {
            name : server_name,
            base64 : base64,
            filename : file_name,
            date : new Date().toLocaleString('ja')
        },
        type : 'POST',
        url : '/add_server'
    })
    .done((data) => {
        if (data.result) {
            // フォームの値 & 画像のプレビューをクリア
            $('#add-server--name').val('');
            $('#error-msg--add-server-name').text('');
            $('#modal--create-server .p-form__filename').text('No file chosen');
            $('#add-server--image').val('');
            if ($('#add-server--image-wrap img')) $('#add-server--image-wrap img').remove();
            // フォームを閉じて、生成したサーバーのUIを追加
            $('#modal-closer--add-server').trigger('click');
            $('#server-board-wrap').append(
                `<div class="p-index__server-board col-sp-12 col-tab-6" id="server-board-${ data.new_srvr.id }" data-id="${ data.new_srvr.id }">
                    <div class="p-index__server-thumnail-wrap">
                        <a href="/server/${ data.new_srvr.id }" class="p-index__server-thumnail">
                            ${ (base64) ? "<img src=" + base64 + ">" : "<p>No Image</p>" }
                        </a>
                        <button class="p-index__server-edit-btn modal-opener toggler"
                        onclick="prefill_server_update(this.parentNode.parentNode); open_modal_with_closer_id('update-server')">
                            <i class="fa-solid fa-gear"></i>
                        </button>
                    </div>
                    <p class="p-index__server-title">${ server_name }</p>
                </div>`);
        } else {
            $('#error-msg--add-server-name').text(data.error_msg.name);
        }
    });
}

const ADD_SERVER_SUBMIT = $('#add-server--submit');
if (ADD_SERVER_SUBMIT.length) {
    ADD_SERVER_SUBMIT.on('click', () => {
        let file = $('#add-server--image')[0].files[0];
        let server_name = $('#add-server--name').val()
        if (typeof file != 'undefined') {
            const READER = new FileReader();
            READER.onload = (e) => {
                execute_server_addition(server_name, e.currentTarget.result, file.name);
            }
            READER.readAsDataURL(file);
        } else {
            execute_server_addition(server_name);
        }
    });
}



// #サーバー編集用 ---------------------------------------------------------------

execute_server_update = (id, server_name, base64='', file_name='', delete_image=false) => {
    $.ajax ({
        data : {
            name : server_name,
            base64 : base64,
            filename : file_name,
            delete_image : delete_image
        },
        type : 'POST',
        url : `/server/${ id }/update`
    })
    .done((data) => {
        if (data.result) {
            if (base64) {
                $(`#server-board-${ id } .p-index__server-thumnail`).html(`<img src=${ base64 }>` );
            } else if (delete_image) {
                $(`#server-board-${ id } .p-index__server-thumnail`).html(`<p>No Image</p>`);
            }
            $(`#server-board-${ id } .p-index__server-title`).text(server_name)
            $('#modal-closer--update-server').trigger('click');
        } else {
            $('#error-msg--update-server-name').text(data.error_msg.name);
        }
    });
}

const UPDATE_SERVER_SUBMIT = $("#update-server--submit");
if (UPDATE_SERVER_SUBMIT.length) {
    UPDATE_SERVER_SUBMIT.on('click', () => {
        // ajax記法の場合、最初にformを開いた際のdata-idで固定されてしまうため(不明)、純jsの取得法にする
        let id = get_element('#update-server-form').dataset.id
        let server_name = $('#update-server--name').val();
        let file = ($('#update-server--image')[0].files.length) ?
            $('#update-server--image')[0].files[0] : null;
        let delete_image = ($('#update-server--delete-image')) ? 
            $('#update-server--delete-image').prop('checked') : null;
        if (file) {
            const READER = new FileReader();
            READER.onload = (e) => {
                execute_server_update(id, server_name, e.currentTarget.result, file.name);
            }
            READER.readAsDataURL(file);
        } else {
            execute_server_update(id, server_name, '', '', delete_image);
        }
    });
}



// #サーバー検索用 (text欄ENTERで発火するため form.js で起動) ------------------------

execute_server_search = () => {
    $('#server-search-bar').prop('disabled', true);
    loading();
    $.ajax ({
        data : { keyword : $('#server-search-bar').val() },
        type : 'POST',
        url : '/search_server'
    })
    .done((data) => {
        const SERVER_LIST_TAG = $('.p-form__server-banner-list').first();
        const SEARCHED_NUM_DISPLAY = $('#found-server-num');
        const CLICK_URGE = $('#click-urge');

        // server_list_tagを空に
        if (SERVER_LIST_TAG.children().length > 0) SERVER_LIST_TAG.empty();

        // 検索結果数を表示
        SEARCHED_NUM_DISPLAY.html(`${data.server_info.length} servers found for "${data.kw}"`);

        // 検索結果があれば各サーバーを参照するhtml等を追加
        if (!data.server_info[0]) {
            if (!CLICK_URGE.hasClass('u-dn')) CLICK_URGE.addClass('u-dn');
        } else {
            if (CLICK_URGE.hasClass('u-dn')) CLICK_URGE.removeClass('u-dn');
            data.server_info.map(s => {
                let img_html = (s.img) ? `<img src="${s.img}" alt="Server画像です">` : '';
                SERVER_LIST_TAG.append(
                    `<li>
                        <a href="/join_server/${s.name}" class="p-form__server-banner">
                            <div class="p-form__searched-server-img-wrap">${ img_html }</div>
                            <div class="p-form__searched-server-info">
                                <p class="server-name">${s.name}</p>
                                <p class="server-population">${s.num} members</p>
                            </div>
                        </a>
                    </li>`
                );
            })
        }
        $('#server-search-bar').prop('disabled', false);
        $('#server-search-bar').trigger('focus');
        loaded();
    });
}



// #サーバー削除用
execute_server_delete = (id) => {
    $.ajax ({
        type : 'GET',
        url : `/server/${ id }/delete`
    })
    .done((data) => {
        if (data.result) {
            $(`#server-board-${ id }`).remove();
            $('#modal-closer--update-server').trigger('click');
        }
    });
}



// #サーバー退出用
execute_server_leave = (id) => {
    $.ajax ({
        type : 'GET',
        url : `/server/${ id }/leave`
    })
    .done((data) => {
        if (data.result) {
            $(`#joined-server-board-${ id }`).remove();
        }
    });
}






/*-------------------------------------------------------
|   チャンネル と メッセージ 双方で使用する 変数 および HELPER  |
-------------------------------------------------------*/

// 既存のメッセージ数
// (メッセージの自動更新にて、既存と追加後のメッセージ数の差分を図るのに用いる)
let msg_list_length = ($('.p-index-main__msg-list').length) ?
    $('.p-index-main__msg-list').children('li').length : 0


// 受け取った単一のメッセージ情報をメッセージ一覧に反映させます
display_message = (msg, sender_b64) => {
    // *** 挿入する添付ファイルのリストを作成 ***
    // 画像リストを作成
    let return_img_li = (b64, s3name, msg_id) => {
        return `<li class="c-menu-wrap flex-item">
                    <img class="u-pointer" src="${ b64 }" onclick="unfold_slide(this)">
                    <div class="c-corner-icon-menu">
                        <button class="c-menu-elm confirm-ajax" data-confirm_txt="添付画像を削除しますか？"
                        data-ajax_func="delete-message-img" data-img_name="${ s3name }" data-msg_id="${ msg_id }">
                            <i class="fa-solid fa-trash-can"></i>
                        </button>
                    </div>
                </li>`;
    }
    let img_list_html = '';
    if (msg.files.img.length == 3) {
        img_list_html += return_img_li(msg.files.img[0].base64, msg.files.img[0].s3name, msg.id);
        img_list_html += 
            `<div class="flex-item-wrap--column">
                ${ return_img_li(msg.files.img[1].base64, msg.files.img[1].s3name, msg.id) }
                ${ return_img_li(msg.files.img[2].base64, msg.files.img[2].s3name, msg.id) }
            </div>`;
    } else {
        msg.files.img.map(img => {
            img_list_html += return_img_li(img.base64, img.s3name, msg.id);
        });
    }

    // 資料リストを作成
    let dcmnt_list_html = '';
    msg.files.dcmnt.map(dcmnt => {
        dcmnt_list_html +=
            `<li class="p-index-main__dcmnt-wrap c-menu-wrap">
                ${ (dcmnt.s3name.includes(".pdf")) ? 
                    "<i class='fa-regular fa-file-pdf'></i>"
                    : "<i class='fa-solid fa-file-signature'></i>"
                }
                <div class="p-index-main__dcmnt-info u-string-shortener">
                    <a href="${ dcmnt.url }" download="${ dcmnt.true_name }" target="_blank" rel="noopener noreferrer">
                        ${ dcmnt.true_name }
                    </a>
                    <p>${ dcmnt.size }</p>
                </div>
                <div class="c-corner-icon-menu">
                    <button class="c-menu-elm confirm-ajax" data-confirm_txt="添付ファイルを削除しますか？"
                    data-ajax_func="delete-message-dcmnt" data-dcmnt_name="${ dcmnt.s3name }" data-msg_id="${ msg.id }">
                        <i class="fa-solid fa-trash-can"></i>
                    </button>
                </div>
            </li>`;
        });

    // 画像・資料リストをファイル全体のwrapに格納
    let file_wrap_html =
        (img_list_html || dcmnt_list_html) ?
        `<div class="p-index-main__msg-file-wrap row">
            ${ (msg.files.img.length) ?
                `<ul class="msg-img-flex-box msg-img-flex-box--${ msg.files.img.length }">${ img_list_html }</ul>`
                : ''
            }
            ${ (msg.files.dcmnt.length) ?
                `<ul class="p-index-main__msg-dcmnt-list">${ dcmnt_list_html }</ul>`
                : ''
            }
        </div>`
        : '';



    // *** メッセージ一覧に新メッセージの内容を反映(上記で生成したファイルリストも統合) ***
    $('.p-index-main__msg-list').append(
        `<li id="msg-${ msg.id }">
            <div class="p-index-main__sender-image-wrap">
                <div class="p-index-main__sender-image c-btn">
                    ${ (sender_b64) ? 
                        `<img src="${ sender_b64 }" alt="${ msg.sender_name }">` 
                        : "<i class='fa-solid fa-user'></i>"
                    }
                </div>
            </div>
            <div class="p-index-main__msg-content">
                <div class="p-index-main__msg-posting-info">
                    <span class="username u-string-shortener">${ msg.sender_name }</span>
                    <span class="date">${ msg.pricise_date }</span>
                    ${ (msg.sender_name == $('#user-info--name').text()) ?
                        `<button class="p-index-main__msg-manager p-index-main__msg-mdl-opener"
                        data-msg_id="${ msg.id }" onclick="open_msg_txtarea(this)">
                            <i class="fa-regular fa-pen-to-square"></i>
                        </button>
                        <button class="p-index-main__msg-manager msg-delete-btn confirm-ajax" data-confirm_txt="メッセージを削除しますか？" data-ajax_func="delete-message" data-msg_id="${ msg.id }">
                            <i class="fa-solid fa-trash-can"></i>
                        </button>`
                        : ''
                    }
                </div>
                ${ (msg.content) ?
                    `<div class="p-index-main__msg-text-wrap">
                        <p>${ msg.content }</p>
                    </div>` : ''
                }
                ${ file_wrap_html }
            </div>
            <div ></div>
        </li>`
    );
}


// 日付を区切るボーダーを挿入します
insert_date_partition = (date) => {
    $('.p-index-main__msg-list').append(
        `<div class="p-index-main__date-partition">${ date }</div>`
    );
}


// 複数メッセージの内容を反映します
reflect_channel_messages = (msg_data, is_chnl_switch=false) => {
    if (!msg_data.messages.length) return;
    let prev_date = msg_data.messages[0].date
    msg_data.messages.forEach((m, index) => {
        // 日付を区切るボーダーを挿入
        // ・チャンネル切替時に先頭メッセージの場合
        // ・直前のメッセージの日付の翌日の場合
        if ( (is_chnl_switch && index == 0) || m.date > prev_date) {
            insert_date_partition(m.date);
            prev_date = m.date;
        }
        display_message(
            m,
            (m.sender_name in msg_data.sender_b64) ?
                msg_data.sender_b64[m.sender_name] : ''
        );
    });
}





