// HELPER -------------------------------------------------------------------

// インプット、アウトプット欄を別のチャンネル用に初期化
set_up_input_output = (chnl_id, chnl_name) => {
    $('.p-index-main__output').html(
        `<div class="p-index-main__output-header">Welcome to<br>${ chnl_name }</div>
        <ul class="p-index-main__msg-list"></ul>`
    )
    $('#add-message-form').data('chnl_id', chnl_id)
}


// インプット、アウトプット欄を無効化
clear_input_output = () => {
    $('.p-index-main__output').empty();
    $('.p-index-main__input').addClass('u-dn');
}


// ヘッダーのトップチャンネルバナーをチャンネルの有無で変更
change_header_channel_banner = (status) => {
    // チャンネルが存在しなくなった場合、チャンネル生成ボタンのみを設置
    if (status == 'clear' ) {
        $('.l-header__channel-banner').remove();
        $('.l-header__channel-list').remove();
        $('.l-header__leftmenu').append(
            `<button class="l-header__channel-banner u-dn-pc col-sp-11" onclick="open_modal_with_closer_id('add-channel')">
                <i class="fa-solid fa-plus fa-chevron-down fa-chevron-up"></i>
                <p class="l-header__channel-name u-string-shortener">Create Channel</p>
            </button>`
        );

    // 空の状態からチャンネルを追加した場合、ヘッダーのチャンネルリストを生成
    } else if (status == 'initial' ) {
        $('.l-header__channel-banner').remove();
        $('.l-header__leftmenu').append(
            `<button class="l-header__channel-banner u-dn-pc col-sp-11 toggler">
                <i class="fa-solid fa-chevron-up"></i>
                <p class="l-header__channel-name u-string-shortener"></p>
            </button>
            <ul class="l-header__channel-list u-dn-pc tgl-reactor" id="header-channel-list">
                <button class="p-index__channel-header modal-opener"
                onclick="open_modal_with_closer_id('add-channel')">
                    <p class="p-index__channel-name">Create Channel</p>
                    <i class="fa-solid fa-plus"></i>
                </button>
            </ul>`
        );
    }
}


// ヘッダー、サイドメニューのチャンネルリストにチャンネルバナーを追加
add_new_channel_banner = (chnl_id, chnl_name) => {
    // サイドメニュー
    $('#side-channel-list').append(
        `<li class="p-index__channel-wrap row" id="side-channel--${ chnl_id }" data-chnl_id="${ chnl_id }">
            <div class="p-index__channel-name-wrap col-tab-20"
            onclick="execute_channel_switch(this.parentNode)">
                <div class="hash-mark"><i class="fa-solid fa-hashtag"></i></div>
                <p class="channel-name u-string-shortener">${ chnl_name }</p>
            </div>
            <button class="p-index__channel-invite-btn col-tab-2">
                <i class="fa-solid fa-user-plus"></i>
            </button>
            <button class="p-index__channel-setting-btn col-tab-2 modal-opener"
            onclick="prefill_channel_update(this.parentNode); open_modal_with_closer_id('update-channel')">
                <i class="fa-solid fa-gear"></i>
            </button>
        </li>`
    );
    // ヘッダー
    $('#header-channel-list').append(
        `<li class="p-index__channel-wrap row" id="header-channel--${ chnl_id }" data-chnl_id="${ chnl_id }">
            <div class="p-index__channel-name-wrap col-sp-20"
            onclick="execute_channel_switch(this.parentNode)">
                <div class="hash-mark"><i class="fa-solid fa-hashtag"></i></div>
                <p class="channel-name u-string-shortener">${ chnl_name }</p>
            </div>
            <button class="p-index__channel-invite-btn col-sp-2">
                <i class="fa-solid fa-user-plus"></i>
            </button>
            <button class="p-index__channel-setting-btn col-sp-2 modal-opener"
            onclick="prefill_channel_update(this.parentNode); open_modal_with_closer_id('update-channel')">
                <i class="fa-solid fa-gear"></i>
            </button>
        </li>`
    );
}


// チャンネルリスト内のバナーのUIを変更
// ・指定id値をID属性に持つチャンネルバナーをactivate
// ・ヘッダーのトップチャンネルバナーをactivateさせたチャンネル名に変更
change_channel_active_status = (id=null, chnl_name=null) => {
    // チャンネルリスト内で現在位置のチャンネルのアクティブステータスをoff
    if ($('.u-active-item')) $('.u-active-item').removeClass('u-active-item');
    if (id) {
        $(`#side-channel--${ id }`).addClass('u-active-item');
        $(`#header-channel--${ id }`).addClass('u-active-item');
    }
    if (chnl_name) $('.l-header__channel-name').text('# ' + chnl_name)
}



// #チャンネル切替用 -------------------------------------------------------------

execute_channel_switch = (elm) => {
    if (elm.classList.contains('u-active-item')) return;
    loading();
    let id = elm.dataset.chnl_id;
    $.ajax ({
        type : 'GET',
        url : `/channel/${ id }/switch`
    })
    .done((data) => {
        if (data.result) {
            change_channel_active_status(id, data.chnl_name);
            set_up_input_output(id, data.chnl_name);
            reflect_channel_messages(data.msg_data, true);
            reset_conifrm_ajax();
            // アウトプット欄最下部までスクロール
            scroll_to_bottom();
            // 切り替えたチャンネルの既存メッセージ数を記憶
            msg_list_length = data.msg_data.messages.length;
        }
        loaded();
    });
}



// #チャンネル追加用 -------------------------------------------------------------

execute_channel_addition = () => {
    chnl_name = $('#add-channel--name').val()
    $.ajax ({
        data : {
            name : chnl_name,
            date : new Date().toLocaleString('ja')
        },
        type : 'POST',
        url : `/server/${ $('#add-channel-form').data('server_id') }/add_channel`
    })
    .done((data) => {
        if (data.result) {
            // チャンネルが存在してなかった場合、input欄を開放、ヘッダーのチャンネルリストを生成
            if (!$('.l-header__leftmenu .p-index__channel-list').length)
                $('.p-index-main__input').removeClass('u-dn');
                change_header_channel_banner('initial');
                // ヘッダーのトップチャンネルバナーがプルダウンを開けるようtoggle機能を付与
                toggle(get_element('.l-header__channel-banner')[0]);

            // 追加したチャンネル仕様にoutput欄、input欄を変更
            set_up_input_output(data.chnl_id, chnl_name);

            // 追加したチャンネルのバナーをチャンネルリストに追加
            add_new_channel_banner(data.chnl_id, chnl_name);

            // 追加したチャンネルのactive statausをオンに
            change_channel_active_status(data.chnl_id, chnl_name);

            // フォームをクリア
            $('#add-channel--name').val('');
            // チャンネルの追加フォームを閉じる
            $('#modal-closer--add-channel').trigger('click');
        } else {
            $('#error-msg--add-channel-name').text(data.error_msg.name);
        }
    });
}

const ADD_CHANNEL_SUBMIT = $("#add-channel--submit");
if (ADD_CHANNEL_SUBMIT.length) {
    ADD_CHANNEL_SUBMIT.on('click', () => {
        execute_channel_addition();
    });
}



// #チャンネル編集用  -------------------------------------------------------------

execute_channel_update = () => {
    id = $('#update-channel-form').data('id');
    chnl_name = $('#update-channel--name').val();
    $.ajax ({
        data : { name : chnl_name },
        type : 'POST',
        url : `/channel/${ id }/update`
    })
    .done((data) => {
        if (data.result) {
            // サイドメニュー、ヘッダーのチャンネルリストにある対象チャンネルの名前を更新
            $(`#side-channel--${ id } .channel-name`).text(chnl_name);
            $(`#header-channel--${ id } .channel-name`).text(chnl_name);

            $('#modal-closer--update-channel').trigger('click');
        } else {
            $('#error-msg--update-channel-name').text(data.error_msg.name);
        }
    });
}

const UPDATE_CHANNEL_SUBMIT = $("#update-channel--submit");
if (UPDATE_CHANNEL_SUBMIT.length) {
    UPDATE_CHANNEL_SUBMIT.on('click', () => {
        execute_channel_update();
    });
}



// #チャンネル削除用  -------------------------------------------------------------
execute_channel_delete = (id) => {
    $.ajax ({
        type : 'GET',
        url : `/channel/${ id }/delete`
    })
    .done((data) => {
        if (data.result) {
            let was_active_item = ($(`#side-channel--${ id }`).hasClass('u-active-item'));

            // サイドバー、ヘッダーのチャンネルリスト内から消去
            $(`#side-channel--${ id }`).remove();
            $(`#header-channel--${ id }`).remove();

            if (was_active_item) {
                // 削除したのが最後のチャンネルなら、output欄を空に、input欄を閉鎖
                if (!$('.p-index__channel-list li').length) {
                    clear_input_output();
                    change_header_channel_banner('clear');
                // 他にチャンネルが存在すれば、先頭のチャンネルに切替
                } else {
                    execute_channel_switch(get_element('.p-index__channel-list li')[0])
                }
            }

            $('#modal-closer--update-channel').trigger('click');
        }
    });
}






