// HELPER -------------------------------------------------------------------

remove_msg_elm = (id) => {
    let prev_elm = $(`#msg-${ id }`).prev();
    let next_elm = $(`#msg-${ id }`).next();
    if (prev_elm.hasClass('p-index-main__date-partition')
    && (!next_elm.length || next_elm.hasClass('p-index-main__date-partition')))
        prev_elm.remove();
    $(`#msg-${ id }`).remove();
}



// #メッセージ追加用 (text欄ENTERで発火するため form.js で起動)  -----------------------

execute_message_addition = (chnl_id, content, file_list=null) => {
    let date = new Date();
    data = {
        content : content,
        file_list : file_list,
        date : date.toLocaleString('ja')
    };
    json = JSON.stringify(data);
    $.ajax ({
        data : json,
        contentType: 'application/json',
        type : 'POST',
        url : `/channel/${ chnl_id }/post_msg`
    })
    .done((data) => {
        if (data.result) {
            // 最終の日付ボーダーから日付が変わっている場合、新しいボーダーを挿入
            if ( !$('.p-index-main__msg-list').children().length
            || ( $('.p-index-main__date-partition').length
                && data.message.date > $('.p-index-main__date-partition').last().text()) 
            ) insert_date_partition(data.message.date);

            // メッセージを反映
            display_message(
                data.message, 
                (data.message.sender_img) ? data.message.sender_img : ''
            );
            // 反映したメッセージのconifrmが起動できるように仕掛けを施す
            let target = get_element(`#msg-${ data.message.id } .confirm-ajax`);
            target.map( act => add_confirm_ajax(act) )

            // output欄の下までスクロール
            scroll_to_bottom();

            // 既存メッセージ数に追加分を加算
            msg_list_length += 1;
        }
    });
}

trigger_message_addition = () => {
    let chnl_id = $('.u-active-item').data('chnl_id');
    let content = $('#add-message--content').val()
    let files = $('#add-message--files')[0].files;
    // メッセージフォームをクリア
    $('#add-message--content').val('');

    if (files.length) {
        let file_list = [];

        let length = files.length;
        for(let i = 0; i < files.length; i++ ){
            const READER = new FileReader();
            let filename = files[i].name;
            READER.onload = (e) => {
                file_list.push({
                    name : filename,
                    base64 : e.currentTarget.result
                });
                if (i === length - 1) {
                    execute_message_addition(chnl_id, content, file_list);
                    clear_msg_file_input();
                }
            }
            READER.readAsDataURL(files[i]);
        }
    } else {
        execute_message_addition(chnl_id, content);
    }
}



// #メッセージ編集用 (text欄ENTERで発火するため form.js で起動)  -----------------------
execute_message_edit = (id) => {
    let content = $('#update-message--content').val().trim()
    $.ajax ({
        data : { content : content },
        type : 'POST',
        url : `/message/${ id }/update`
    })
    .done((data) => {
        if (data.result) {
            $(`#msg-${ id } .p-index-main__msg-text-wrap p`).first().text(content);
            close_msg_txtarea();
        }
    });
}



// #メッセージ削除用  -------------------------------------------------------------
execute_message_delete = (id) => {
    $.ajax ({
        type : 'GET',
        url : `/message/${ id }/delete`
    })
    .done((data) => {
        if (data.result) {
            remove_msg_elm(id);
        }
    });
}



// #メッセージファイル削除用  -------------------------------------------------------

execute_message_file_delete = (type, msg_id, file_name, target) => {
    $.ajax ({
        type : 'GET',
        url : `/file/${ file_name }/delete`
    })
    .done((data) => {
        if (data.result) {
            target.remove();
            if (type == "img") {
                let img_box = $(`#msg-${ msg_id } .msg-img-flex-box`);
                let imgs = $(`#msg-${ msg_id } .msg-img-flex-box img`);
                if (imgs.length) {
                    img_box.removeClass();
                    img_box.addClass(`msg-img-flex-box msg-img-flex-box--${imgs.length}`)
                    if (imgs.length == 2 || imgs.length == 3) {
                        let flex_items = $(`#msg-${ msg_id } .msg-img-flex-box .flex-item`);
                        img_box.empty();
                        if (imgs.length == 2) {
                            for (let i=0; i<flex_items.length; i++) {
                                img_box.append(flex_items[i]);
                            }
                        } else {
                            var column_wrap = $('<div>').addClass('flex-item-wrap--column');
                            column_wrap.append(flex_items[1], flex_items[2]);
                            img_box.append(flex_items[0], column_wrap);
                        } 
                    }
                } else {
                    img_box.remove();
                }

            } else if (type == 'dcmnt') {
                let dcmnts = $(`#msg-${ msg_id } .p-index-main__msg-dcmnt-list .p-index-main__dcmnt-wrap`);
                if (!dcmnts.length) {
                    $(`#msg-${ msg_id } .p-index-main__msg-dcmnt-list`).remove();
                }
            }

            // テキスト欄、画像欄、資料欄が無い場合、メッセージを削除
            if (!$(`#msg-${ msg_id } .msg-img-flex-box`).length
            && !$(`#msg-${ msg_id } .p-index-main__msg-dcmnt-list`).length
            && !$(`#msg-${ msg_id } .p-index-main__msg-text-wrap`).length) {
                remove_msg_elm(msg_id);
            }
        }
    });
}



// #メッセージ自動更新 -------------------------------------------------------------

execute_update_message_list = () => {
    // メッセージリストが存在しない(チャット画面がない)場合、自動更新を無効
    if (!$('.p-index-main__msg-list').length) return;
    console.log("hey");

    let msg_list = $('.p-index-main__msg-list').children();
    let latest_id = (msg_list.length) ?
        msg_list.last().attr('id').slice(4) : 0

    $.ajax ({
        data : { latest_id : latest_id },
        type : 'POST',
        url : `/channel/${ $('.u-active-item').first().data('chnl_id') }/auto_update`
    })
    .done((data) => {
        if (data.result) reflect_channel_messages(data.msg_data);
    });
}

