const UPDATE_USER_IMAGE_FORM = $('#update-account--image-form');
const MODAL_CLOSER_USER_IMAGE = $('#modal-closer--update-account-image');

execute_account_image_update = (form_data) => {
    $.ajax ({
        data : form_data,
        type : 'POST',
        url : '/update_account_image'
    })
    .done((data) => {
        if (data.result) {
            if (form_data.b64) {
                let img_html = `<img src="${ form_data.b64 }" alt="" data-status="off">`;
                USER_IMAGE.html(img_html);
                if (SIDE_USER_IMAGE.length) SIDE_USER_IMAGE.html(img_html);
            } else {
                let icon_html = `<i class='fa-solid fa-user'></i>`;
                USER_IMAGE.html(icon_html);
                if (SIDE_USER_IMAGE.length) SIDE_USER_IMAGE.html(icon_html);
            }
            MODAL_CLOSER_USER_IMAGE.trigger('click');
            toggle_form_disabled(UPDATE_USER_IMAGE_FORM);
        }
    });
}

