// slide.js

unfold_slide = (img_tag) => {
	let img_list_tag = img_tag.parentNode.parentNode;
	let imgs = get_element('li img', img_list_tag);
	slide_elm_html = '';
	imgs.map(img => {
		slide_elm_html +=
			`<li class="splide__slide">
				<img src="${img.src}">
			</li>`;
	})
	slide_html =
		`<div class="splide__track">
			<ul class="splide__list">
				${slide_elm_html}
			</ul>
		</div>`;

	const SLIDE = get_element('.splide')[0];
	const SLIDE_WRAP = get_element('.p-slide-wrap')[0];
	SLIDE.innerHTML = slide_html
	SLIDE_WRAP.dataset.status = 'on';
	OVERLAY.dataset.status = 'on';
	BODY.dataset.status = 'on';

	let type = (imgs.length > 1) ? 'loop' : '';
	let start_index = imgs.indexOf(img_tag);
	let splide = new Splide( '.splide' , {
		type  : type,
		drag  : 'free',
		snap  : true,
		start : start_index
	} );
	splide.mount();
}


close_slide = (closer) => {
	const SLIDE_WRAP = closer.parentNode;
	SLIDE_WRAP.dataset.status = 'off';
	OVERLAY.dataset.status = 'off';
	BODY.dataset.status = 'off';
}